h1 {
    color: #333;
    font-size: 32px;
    font-weight: 700;
    text-align: center;
    margin-bottom: 70px;
}

h2 {
    color: #555;
    font-size: 20px;
    font-weight: 700;
    text-align: center;
    margin-bottom: 50px;
}

h3 {
    color: #555;
    font-size: 18px;
    font-weight: 700;
    text-align: center;
    margin-top: 70px;
    margin-bottom: 50px;
}

p {
    color: #555;
    font-size: 16px;
    line-height: 1.5;
    font-weight: 500;
    margin-bottom: 20px; 
}

.refLink {
    text-align: left;
    margin-bottom: 20px;
}

.containerBonus {
    padding: 70px;
}

.num {
    color: #2702fc;
    font-weight: 600;
}

.tableTools {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
    margin-bottom: 30px;   
}

.table {
    width: 900px;
    border-collapse: collapse;
    font-size: 14px;
    line-height: 1.5;
    text-align: center;
}

.td, .th {
    border: 1px solid #cfcccc;
    padding: 10px;
    width: auto;
}

.button {
    display: block;
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
    border: 1px solid #ffffff;
    border-radius: 20px;
    padding: 0 30px;
    width: auto;
    height: 60px;
    text-align: center;
    text-overflow: ellipsis;
    font-size: 16px;
    font-weight: 700;
    line-height: calc(16/14);
    overflow: hidden;
    outline: none;
    cursor: pointer;
    color: #000000;
    background-color: rgb(255, 255, 0);
  }
  
  .button:hover,
  .button:focus {
    color: #0d30f5;
  }